}

.rd:after {
    content: '\2807';
    font-size: 16px;
    margin-left: 10px;
    color: silver;
}

.circle-icon {
    display: inline-block;
    width: 30px;
    height: 30px;
    line-height: 30px;
    border-radius: 50%;
    box-shadow: 0px 0px 2px #888;
    vertical-align: middle;
    text-align: center;
    background-color: #dfdddd;
}

.rd-btn:focus {
    color: rgb(33, 172, 44);
}




